.sub-container {
    display: grid;
    grid-gap: 0.5rem;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: 1fr 12fr;
    grid-template-areas: "header header" "aside map";
    height: 100%;
    width: 100%;
    position: absolute;
}

@media  (max-width:620px){
    .sub-container{
        grid-template-areas:"sm-menu header" "map map";
    }
}

/*
* SM MENU (displayed only for small screen)
*/
.sm-menu{
    grid-area:sm-menu;
    background-color: #CEE5E8;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.sm-menu ul{
    cursor: pointer;
    width: 50%;
    text-decoration: none;
    list-style: none;
    margin: 0px;
    padding: 0px;
    -ms-flex-item-align: center;
        align-self: center;
   /* display: flex;
    flex-direction: column;*/
    
}

.sm-menu ul li{
    width: 100%;
    height: 4px;
    background-color: rgba(87, 87, 87, 0.88);
    margin-bottom: 4px;
    
}

/*
* HEADER
*/

.header {
    grid-area: header;
    background-color: #CEE5E8;
    text-align: center;
}

.header h1{
    margin-bottom:10px;
}

.header-details{
    margin-top:5px;
}

/*
* MAP
*/

.leaflet-container {
    height: 90vh;
    width: 100%;
    grid-area: map;
    z-index: 1;
}

/* POP UP */
.leaflet-popup-content{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.leaflet-popup-content h2{
    font-weight:700;
    color: #f7cd59;
    text-align: center;
}

.popup-date{
    text-align: center;
    font-size: 13px;
}

.popup-place{
    text-align: center;
    font-size: 14px;
}

.popup-description{
    text-align: justify;
    font-size: 13px;
}

.popup-lien{
    font-style: italic;
    text-align:center;
}

/* from https://jasonsalzman.github.io/react-add-to-calendar/ */
.react-add-to-calendar {
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
    text-align:center;
    margin-top:20px;
}

.react-add-to-calendar__wrapper {
    cursor: pointer;
}


.react-add-to-calendar__button {
    padding: 10px;
    background-color: #f7cd59;
    border: 1px solid #aab9d4;
    border-radius: 3px;
    color: #000;
}

.react-add-to-calendar__button span{
    color:black;
}

.react-add-to-calendar__dropdown {
    margin-top:12px;
}

.react-add-to-calendar__dropdown ul {
    list-style-type: none;
    margin: 0;
    padding: 0; 
}

.react-add-to-calendar__dropdown ul li{
    margin-top:3px;
}

.react-add-to-calendar__dropdown ul li a{
    text-decoration: none;
    color:black;
}

.fa {
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
}

/*
* ASIDE with LIST VIEW and LOCATION FILTER
*/

.aside {
    grid-area: aside;
    background-color: #f7cd59;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    max-height: 100%;
    overflow-y: auto;
}

@media  (max-width:620px){
    .aside{
        display:none;
    }
    
    .sm-aside-open{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    position: fixed; 
    width: 100%; 
    height: 100%; 
    top: 0; 
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #f7cd59; 
    z-index: 10; 
    cursor: pointer;
    overflow-y: auto;
    }
    
  /*  .leaflet-container{
        display:none
    }*/
    
}



/*
* LOCATION FILTER
*/

.location-filter {
    width: 100%;
    text-align: center;
}

.location-filter input {
    height: 30px;
    width: 90%;
    display: block;
    margin: 12px auto;
}

.location-filter .list-sorted-type{
    font-size:12px;
}

/*
* LIST VIEW
*/

.list-view {
    list-style-type: none;
    margin: 0;
    padding: 0;
    margin-top: 20px;
    text-align:center;
}

.list-view li {
    margin-bottom: 10px;
}

.list-view .list-date{
    font-size: 13px;
}

.list-view .list-name{
    font-size: 15px;
    
}

.list-view .list-location{
    font-size: 14px;
}




